import './CommentForm.scss';
import avatar from '../../assets/images/Mohan-muruge.jpg';
import commentIcon from '../../assets/images/icons/add_comment.svg'
import CTAButton from '../CTAButton/CTAButton';
import { Component } from 'react';

class CommentForm extends Component {
    
    render() {
        let { comments, handleAddComment, fieldError } = this.props

        return (
            <section className="comment-form__section">
                <h3 className="comment-form__heading">{comments.length} Comments</h3>
    
                <div className="comment-form__container">
                    <div className="comment-form__avatar-wrapper">
                        <img src={avatar} alt="avatar" className="comment-form__avatar" />
                    </div>
                    
                    <form onSubmit={handleAddComment} className="comment-form">
                        
                        <div className="comment-form__label-and-textarea">
                            <label htmlFor="comment"><p className="comment-form__subheading">Join the conversation</p></label>
                            <textarea 
                                name="comment" 
                                className={`comment-form__comment ${fieldError ? "comment-form__comment--error" : ""}`}
                                placeholder="Add a new comment"
                                ></textarea>
                        </div>
                        
                        <div className="comment-form__button-wrapper">
                            <CTAButton src={commentIcon} text="comment" />
                        </div>
    
                    </form>
                </div>
            </section>
        )
    }
}

export default CommentForm;