import './VideoDetails.scss';
import { epochToMMDDYYYY } from '../../utils/utils';
import viewsIcon from '../../assets/images/icons/views.svg';
import likesIcon from '../../assets/images/icons/likes.svg';
import { Component } from 'react';

class VideoDetails extends Component {
    
    render() {
        let { selectedVideo, handleVideoLike } = this.props
        return (
            <section className="video-details">
                <div className="video-details__title">{selectedVideo.title}</div>
                <div className="video-details__info-wrapper">
                    <div className="video-details__channel-date">
                        <p className="video-details__channel">By {selectedVideo.channel}</p>
                        <p className="video-details__date">{epochToMMDDYYYY(selectedVideo.timestamp)}</p>
                    </div>
                    <div className="video-details__views-likes">
                        <p className="video-details__views">
                            <img className="video-details__icon" src={viewsIcon} alt="views icon" />
                            &nbsp; {selectedVideo.views}</p>
                        <p onClick={handleVideoLike} className="video-details__likes">
                            <img className="video-details__icon" src={likesIcon} alt="likes icon" />
                            &nbsp; {selectedVideo.likes}</p>
                    </div>
                </div>
                <p className="video-details__description">{selectedVideo.description}</p>
            </section>
        )
    }
}

export default VideoDetails;