import { Component } from "react"
import axios from "axios"
import { localAPI } from "../../utils/apiUtils"
import CommentForm from "../../components/CommentForm/CommentForm"
import CommentSection from "../../components/CommentSection/CommentSection"
import NextVideos from "../../components/NextVideos/NextVideos"
import VideoDetails from "../../components/VideoDetails/VideoDetails"
import VideoPlayer from "../../components/VideoPlayer/VideoPlayer"

class VideoPlayerPage extends Component {

  state = {
    videos: [],
    selectedVideo: {},
    fieldError: false
  }

  // axios get request for video details for a given id
  getSelectedVideo = (id) => {
    axios
        .get(localAPI + id)
        .then(response => {
          this.setState({
            selectedVideo: response.data
          })
        })
  }

  componentDidMount() {
    axios
      .get(localAPI)
      .then(response => {
        
        // if URL paramater provided, set to that, if not, default to id of first object in videos array from API
        const id = this.props.match.params.id || response.data[0].id
        
        this.setState({
          videos: response.data
        })

        this.getSelectedVideo(id)
      })
  }

  componentDidUpdate(prevProps) {
    let prevId = prevProps.match.params.id
    // if no URL params provided (ie Home route) default to id of first video in videos array
    const currId = this.props.match.params.id || this.state.videos[0].id

    // when using home route, prevId becomes undefined, setting infinite loop in below logic
    // setting prevId also to first video in array stops getSelectedVideo from calling below, once the default video is loaded
    if (!prevId) {
      prevId = this.state.videos[0].id
    }

    // check if new id from URL is different to previous before calling getSelectedVideo which sets state
    if (String(currId) !== String(prevId)) {
      this.getSelectedVideo(currId)
      window.scrollTo(0, 0);
    }
  }

  handleAddComment = (event) => {
    event.preventDefault();
    
    // form validation
    if (!event.target.comment.value) {
      return this.setState({
        fieldError: true
      })
    }

    let form = event.target
    let newComment = {}
    let videoId = this.state.selectedVideo.id
    newComment.name = "Anonymous"
    newComment.comment = event.target.comment.value

    this.setState({
      fieldError: false
    })

    axios
      .post(localAPI + videoId + "/comments", newComment)
      .then(() => {
        form.reset()
        this.getSelectedVideo(videoId)
      })
  }

  handleDelete = (event) => {
    let commentId = event.target.id
    let videoId = this.state.selectedVideo.id

    // delete endpoint: DELETE /videos/:videoId/comments/:commentId
    let deleteURL = `${localAPI}${videoId}/comments/${commentId}`;
      axios
          .delete(deleteURL)
          .then(() => {
              this.getSelectedVideo(videoId)
          })
          .catch(error => console.log(error))
  }

  handleVideoLike = () => {
    let videoId = this.state.selectedVideo.id;
    axios
        .put(localAPI + videoId + "/likes")
        .then(() => {
            this.getSelectedVideo(videoId)
        })
  }

render() {

  const { videos, selectedVideo } = this.state
  const filteredVideos = videos.filter(video => video.id !== selectedVideo.id)
  
  // check that both api requests have returned and updated state 
  if (!this.state.videos.length || !this.state.selectedVideo.id) {
    return <div>Loading...</div>
  }

    return (
        <div>
            <VideoPlayer selectedVideo={selectedVideo}/>
            <section className="details-comments-next-container">
              <div className="details-comments-wrapper">
                <VideoDetails selectedVideo={selectedVideo} handleVideoLike={this.handleVideoLike}/>
                <CommentForm 
                  comments={selectedVideo.comments} 
                  handleAddComment={this.handleAddComment}
                  fieldError={this.state.fieldError}/>
                <CommentSection 
                  comments={selectedVideo.comments}
                  handleDelete={this.handleDelete}/>
              </div>
              <NextVideos videos={filteredVideos}/>
            </section>
        </div>
    )
  }
}

export default VideoPlayerPage;