import './CommentSection.scss'
import epochToDynamic from '../../utils/utils';
import deleteIcon from '../../assets/images/icons/icon-delete.svg';

function CommentSection({ comments, handleDelete }) {

    comments.sort((a, b) => b.timestamp - a.timestamp);

    return(
        <>
        <div className="comment__container">
            {comments.map(commentObj => 
                <div className="comment__wrapper" key={commentObj.id}>
                    <div className="comment__avatar-delete-wrapper">
                        <div className="comment__avatar"></div>
                        <img id={commentObj.id} src={deleteIcon} alt="delete" className="comment__delete" onClick={handleDelete}/>
                    </div>
                    <div className="comment__area">
                        <div className="comment__nameDate">
                            <p className="comment__name">{commentObj.name}</p>
                            <p className="comment__date">{epochToDynamic(commentObj.timestamp)}</p>
                        </div>
                        <p className="comment__text">{commentObj.comment}</p>
                    </div>
                </div>
            )}
        </div>
        </>
    )
}

export default CommentSection;