import CTAButton from '../../components/CTAButton/CTAButton';
import publish from '../../assets/images/icons/publish.svg'
import './VideoUpload.scss';
import { Link } from 'react-router-dom';
import thumbnail from '../../assets/images/Mohan-muruge.jpg';
import { Component } from 'react';
import axios from 'axios';
import { localAPI } from '../../utils/apiUtils';

class VideoUpload extends Component {
    
    handleUpload = (event) => {
        event.preventDefault()

        let form = event.target
        let videoUploadObject = {
            "title": event.target.title.value,
            "channel": "Username",
            "image": "/images/image0.jpeg",
            "description": event.target.description.value,
        }

        axios
          .post(localAPI, videoUploadObject)
          .then(() => {
            form.reset()
            alert("Upload Successful")
            window.location.href = "/";
          })
    }

    render() {
        return (
            <div className="video-upload__container">
                <h1 className="video-upload__title">Upload Video</h1>
                <div className="video-upload__thumbnail-form-wrapper">
                    <div className="video-upload__thumbnail-wrapper">
                        <h3 className="video-upload__subheading">Video Thumbnail</h3>
                        <img src={thumbnail} alt="video thumbnail" className="video-upload__thumbnail" />
                    </div>
                    <form onSubmit={(event) => {this.handleUpload(event)}} className="video-upload__form">
                        <label className="video-upload__subheading"> Title your video <br/>
                            <input className="video-upload__title-input" type="text" name="title" placeholder="Add a title to your video"></input><br/>
                        </label>
                        <label className="video-upload__subheading"> Add a video description <br/>
                            <textarea className="video-upload__description" name="description" placeholder="Add a description to your video"></textarea>
                        </label>
                        <div className="video-upload__links-wrapper">
                                <CTAButton src={publish} text="publish" />
                            <Link to="/" className="video-upload__cancel">Cancel</Link>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default VideoUpload;
