import { Link } from 'react-router-dom';
import './NextVideos.scss';

function NextVideo({ videos }) {
    return (
        <section className="next-video__section">
            <h3 className="next-video__heading">NEXT VIDEOS</h3>
            {videos.map(video => (
                <Link to={"/VideoDetails/" + video.id} key={video.id}>
                    <div className="next-video__wrapper">
                        <div className="next-video__image-container">
                            <img className="next-video__thumbnail" src={video.image} alt={video.title}/>
                        </div> 
                        <div className="next-video__text-wrapper">
                            <p className="next-video__title">{video.title}</p>
                            <p className="next-video__name">{video.channel}</p>
                        </div>
                    </div>
                </Link>
            ))}
        </section>
    )
}

export default NextVideo;